@import url(https://fonts.googleapis.com/css?family=Lobster);
@import url(https://fonts.googleapis.com/css?family=Baloo);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
    font-family:Helvetica;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
.content {
 
  height:100vh;
  width:100vw;
}
h1 {
  font-size: 40px;
  color: white;
  font-weight: bold;
}
header {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: 10vh;
  background-color:rgb(87, 43, 7);
  z-index:4;
  padding: 40px;
  font-family: 'Lobster', cursive;
  padding-left: 100px;
  padding-right: 100px;
}
header img{
  height: 8vh;
  width: 5vw;
  position:fixed;
  left:150px;
}
header h1{
  position:fixed;
  left:225px;
}
.main-content {
  display: flex;
  justify-content: center;
  align-items: center;
  height:100vh;
  width:100vw;
  margin-top:3%;
}
@media only screen and (max-width : 600px) {
  header{
    justify-content: center;
    height: 5vh;
  }
  header img{
    width: 10vw;
    left: 8px;
  }
  header h1{
    left:47px;
  }
}
@media only screen and (max-width : 400px) {
  header{
    justify-content: center;
  }
  header img{
    width: 10vw;
  }
}
.login {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url(/ExpandMyLifeSpan/static/media/chopping-board-nav.aa7cf8e4.jpg); 
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.login-content {
    height: 70vh;
    width: 50vw;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background-color: white;
    border-radius: 20px;
    border: solid 4px rgb(88, 40, 8);
}
form {
    height: 40vh;
    width: 40vw;
    padding: 5px;
}

.field-groups {
    height: 52vh;
    width: 40vw;
    padding: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.fields {
    text-align: left;
    width: 30vw;
    height: 10vh;
}
input {
    width: 18vw;
    height: 6vh;
    border-radius: 10px;
    font-size: 13px;
    font-weight: bold;
    float: right;
}
input::-webkit-input-placeholder {
    text-align: center;
}
input::-moz-placeholder {
    text-align: center;
}
input:-ms-input-placeholder {
    text-align: center;
}
input::-ms-input-placeholder {
    text-align: center;
}
input::placeholder {
    text-align: center;
}
input[type=submit],.submit{
   border: solid 1px rgb(154, 190, 25);
   background-color:rgb(154, 190, 25);
   color: black;
   font-weight: bold;
   font-size: 20px;
   width: 10vw;
}
.submit{
    width: 20vw;
}
button:disabled,
button[disabled]{
  
  background-color: rgba(210, 230, 179, 0.479);
  
}
input[type=submit]:hover, input[type=submit]:active,.submit:hover, .submit:active {
    border: solid 1px rgb(231, 45, 12);
    background-color:rgb(231, 45, 12);
    color: white;
    }

label {
    font-size: 20px;
    font-weight: bold;
 
   
}
.submit {
    display: flex;
    justify-content: center;
    align-items: center;
}
.sign-up-content {
    height: 10vh;
    width: 30vw;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    /* align-items: center; */
}
.sign-up-content div{
    height: 10vh;
    width: 15vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.sign-up-content a {
    text-decoration: none;
    position: relative;
    left: 10px;
    bottom: 2px;
    font-weight:bold;
    font-size: 18px;
}
@media only screen and (max-width : 600px) {
    .login-content {
        width: 90vw;
    }  
    form {
        height: 60vh;
        width: 85vw;
    }
    .field-groups {
        width: 80vw;
    }
    .fields {
        width: 80vw;
    }
    input {
        width: 80vw;
    }
    input[type=submit]{
        width: 50vw;
        font-family: 'Baloo', cursive;
    }
    .sign-up-content {
        width: 60vw;
    }
}
@media only screen and (max-width : 400px) {
    .login{
        justify-content: flex-start;
    }
    .login-content {
        height: 70vh;
        width: 96vw;
    }
    form {
        height: 60vh;
        width: 90vw;
    }
    .field-groups {
        width: 90vw;
    }
    .fields {
        width: 80vw;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
    }
   
    input {
        width: 80vw;
    }
    input[type=submit]{
        width: 50vw;
    }
    .sign-up-content {
        width: 60vw;
    }
    .submit {
        font-size:12px;
    }
}
